.list-group-item {
     text-decoration: none !important;
}

.panel-title {
     cursor: pointer;
}
h4.tab-title {
     font-family: "avenirheavy", Helvetica, Arial, "sans-serif";
     font-weight: normal;
     font-size: 22px;
     color: #ffffff;
}
.vertab-content ul,
.vertab-content ol {
     padding-left: 15px;
}
@media (min-width: 768px) {
     .vertab-container {
          z-index: 10;
          background-color: #7952b3;
          padding: 0 !important;
          border: 1px solid #ddd;
          background-clip: padding-box;
          opacity: 0.97;
          filter: alpha(opacity=97);
          overflow: auto;
          margin-bottom: 50px;
     }
     .vertab-menu {
          padding-right: 0;
          padding-left: 0;
          padding-bottom: 0;
          display: block;
          background-color: #e3e3e3;
     }
     .vertab-menu .list-group {
          margin-bottom: 0;
     }
     .vertab-menu .list-group > a {
          margin-bottom: 0;
          border-radius: 0;
     }
     .vertab-menu .list-group > a,
     .vertab-menu .list-group > a {
          color: #818181;
          background-image: none;
          background-color: #f6f6f6;
          border-radius: 0;
          box-sizing: border-box;
          border: none;
          border-bottom: 1px solid #cacaca;
          padding: 15px 10px;
          font-size: 11px;
     }
     .vertab-menu .list-group > a.active,
     .vertab-menu .list-group > a:hover,
     .vertab-menu .list-group > a:focus {
          position: relative;
          border: none;
          border-radius: 0;
          border-bottom: 1px solid #cacaca;
          border-left: 5px solid #7952b3;
          padding-left: 5px;
          background-image: none;
          background-color: #f6f6f6;
          color: #7952b3;
     }
     .vertab-content {
          padding-left: 20px;
          padding-top: 10px;
          color: #ffffff;
     }

     .vertab-container .panel-heading {
          display: none;
     }
     .vertab-container .panel-body {
          border-top: none !important;
     }
}

/* If the tc_breakpoint variable is changed, this breakpoint should be changed as well */
@media (max-width: 767px) {
     .vertab-container {
          margin-bottom: 20px;
     }
     .vertab-container .vertab-menu {
          display: none;
     }
     .vertab-container .panel-heading {
          background-color: #f6f6f6;
          color: #818181;
          padding: 15px;
          border-bottom: 1px solid #f6f6f6;
          border-top-left-radius: 0;
          border-top-right-radius: 0;
          border-left: 5px solid #f6f6f6;
     }
     .vertab-container .panel-heading:hover,
     .vertab-container .panel-heading:focus,
     .vertab-container .panel-heading.active {
          border-left: 5px solid #7952b3;
          border-bottom: 1px solid #7952b3;
     }
     .vertab-content {
          border-bottom: 1px solid #cacaca;
     }
     .vertab-container .panel-title a:focus,
     .vertab-container .panel-title a:hover,
     .vertab-container .panel-title a:active {
          color: #818181;
          text-decoration: none;
     }
     .panel-collapse.collapse,
     .panel-collapse.collapsing {
          background-color: #7952b3 !important;
          color: #ffffff;
     }
     .vertab-container .panel-collapse .panel-body {
          border-top: none !important;
     }
}
